* {
  margin: 0;
  padding: 0;
}

header {
  width: 100%;
  height: 100px;
  background-color: red;
  box-shadow: 0 2px 4px #000;
}

.container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
 
}

.hero {
  width:500px;
  margin: 20px;
  border: 1px solid #000;
  border-radius: 10px;
  background-color: white;
  box-shadow: 2px 2px 4px #000;
  transition: all 0.4s;
}

.hero:hover {
  background-color: #000;
}

.hero:hover img {
  border: 1px solid #FFF;
}

.hero:hover a {
  color: #FFF;
}

#search {
  display: block;
  margin: 25px auto;
  width: 200px;
  height: 20px;
  transition: transform 0.6s;
}

#search:focus {
  transform: scale(1.2)
}

h1 {
  line-height: 100px;
  text-align:center;
  color: white;
  text-shadow: 2px 2px 3px #000;
}

h2 {
  margin: 25px 0;
  text-align: center;
  font-variant: small-caps;
}

.hero img {
  width: 300px;
  display: block;
  margin: 0 auto 25px auto; 
  border: 1px solid #000;
  border-radius: 10px;
}

a {
  text-decoration: none;
  color: #000;
}

/* LOADING */
.load {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.shield {
  width: 205px;
  animation: pulse 1s infinite linear;
}

@keyframes pulse{
  0% {transform: scale(1)}
  50% {transform: scale(1.1)}
  100% {transform: scale(1)}
}